import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { HttpModule } from '@angular/http';

import { OrdersComponent } from '../components/orders/orders';

import { HomePage } from '../pages/home/home'
import { LoginPage } from '../pages/login/login'
import { RegisterPage } from '../pages/register/register'
import { SellerDashboardPage } from '../pages/seller-dashboard/seller-dashboard'
import { CustomerDashboardPage } from '../pages/customer-dashboard/customer-dashboard'
import { StorePage } from '../pages/store/store'
import { ThankyouPage }from '../pages/thankyou/thankyou'

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SQLite } from '@ionic-native/sqlite'
import { SQLitePorter } from '@ionic-native/sqlite-porter'
import { DatabaseProvider } from '../providers/database/database';
import { IonicStorageModule } from '@ionic/storage';

//mocks
import { SQLiteMock } from '@ionic-native-mocks/sqlite'
import { SQLitePorterMock } from '@ionic-native-mocks/sqlite-porter'
import { AuthProvider } from '../providers/auth/auth';
import { RequestProvider } from '../providers/request/request';
import { UserProvider } from '../providers/user/user';
import { StoreProvider } from '../providers/store/store';
import { OrderProvider } from '../providers/order/order';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    RegisterPage,
    SellerDashboardPage,
    CustomerDashboardPage,
    OrdersComponent,
    StorePage,
    ThankyouPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/seller-dashboard/seller-dashboard.module#SellerDashboardPageModule', name: 'SellerDashboardPage', segment: 'seller-dashboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/thankyou/thankyou.module#ThankyouPageModule', name: 'ThankyouPage', segment: 'thankyou', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/store/store.module#StorePageModule', name: 'StorePage', segment: 'store', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/customer-dashboard/customer-dashboard.module#CustomerDashboardPageModule', name: 'CustomerDashboardPage', segment: 'customer-dashboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
    HttpModule,    
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    RegisterPage,
    SellerDashboardPage,
    CustomerDashboardPage,
    StorePage,
    ThankyouPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    DatabaseProvider,
    {provide: SQLite, useClass: SQLiteMock},
    {provide: SQLitePorter, useClass: SQLitePorterMock },
    // SQLite,
    // SQLitePorter,
    AuthProvider,
    RequestProvider,
    UserProvider,
    StoreProvider,
    OrderProvider
  ]
})
export class AppModule {}



